

.header_main
{
  font-size: 200%;
}
.header_ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: rgb(0, 80, 117);
  }
  
  .header_li {
    float: left;
  }
  
  .header_li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }
  
  .header_li a:hover   {
    background-color: rgb(6, 117, 168);
  }
  .header_main a.active{
    background-color: rgb(6, 117, 168);

  }

  
  .header_anchorIcon
  {
    position: absolute;
    /* margin:1%; */
    right: 1%;
    top:1%;
  }